body {
  letter-spacing: .2vw;
  color: #000000b9;
  background-color: #f1f1ee;
  margin: 0 16px 16px;
  font-family: Manrope, cursive;
  font-style: normal;
  font-weight: 300;
  line-height: 1.5;
  overflow-x: hidden;
}

.firstWindow {
  text-align: center;
  font-family: Aboreto, cursive;
}

.avatar {
  max-width: 70%;
  width: auto;
}

.row {
  flex-direction: row;
}

.row-reverse {
  flex-direction: row-reverse;
}

.column {
  flex-direction: column;
}

.noVision {
  opacity: 0;
}

a {
  color: #000000b9;
  text-decoration: none;
}

h1 {
  margin: .2em;
  font-size: 5vw;
  font-weight: lighter;
}

h2 {
  font-size: 4.5vw;
  font-weight: lighter;
}

.vol {
  display: flex;
}

div.vol:nth-child(n+1) {
  width: 100%;
  height: -moz-fit-content;
  height: fit-content;
}

.text {
  text-align: center;
  width: 100%;
  justify-content: center;
  align-items: center;
  margin: auto;
  display: flex;
}

.text h3 {
  letter-spacing: .15vw;
  font-size: 3vw;
  font-style: normal;
  font-weight: 300;
}

.text p {
  font-size: 2.5vw;
}

.volume {
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 5vw;
  display: flex;
}

.picture {
  width: 70%;
  height: 70vw;
  background-size: 100%;
  margin: auto;
}

.first {
  background-image: url("vol1.9e6d65ad.jpg");
}

.second {
  background-image: url("vol2.2234db70.jpg");
}

.third {
  background-image: url("vol3.b0e858c6.jpg");
}

@media (min-width: 1024px) {
  .text {
    width: 50%;
  }

  .text h3 {
    letter-spacing: .15vw;
    padding-left: 3vw;
    padding-right: 3vw;
    font-size: 1.7vw;
    font-style: normal;
    font-weight: 300;
  }

  .avatar {
    max-width: 30%;
  }

  .text p {
    padding-left: 3vw;
    padding-right: 3vw;
    font-size: 1vw;
  }

  h1 {
    margin: .2em;
    font-size: 4vw;
    font-weight: lighter;
  }

  h2 {
    font-size: 2.5vw;
    font-weight: lighter;
  }

  .picture {
    width: 50%;
    height: 50vw;
    margin: 0;
  }

  .text {
    margin: 0;
  }
}

.carousel-photo {
  width: 100%;
  height: calc(40vh + 7vw);
  object-fit: cover;
}

.logo {
  color: #f1f1ee;
  margin: 0;
  padding: .5vw;
  font-size: 3vw;
}

.right {
  text-align: end;
}

.portfolio {
  opacity: 0;
  visibility: hidden;
  margin-bottom: 20px;
  transition: opacity 2s, visibility 0s linear 5s;
}

.widget {
  opacity: 1;
  visibility: visible;
  transition-delay: 0s;
}

.header-photo {
  letter-spacing: 4vw;
  width: 60%;
  justify-content: center;
  align-items: center;
  margin: auto;
  font-size: 4vw;
  font-weight: 500;
  line-height: 87px;
  display: flex;
}

.header-photo p {
  margin-right: -4vw;
}

.photo {
  flex-direction: row;
  display: flex;
}

.icons {
  width: 40%;
  flex-direction: row;
  justify-content: center;
  margin: auto;
  display: flex;
  position: relative;
}

.social-footer-icons {
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  display: flex;
  position: relative;
}

.fa {
  font-size: 8vw;
}

@media (min-width: 1024px) {
  .fa {
    font-size: 3vw;
  }
}

.fa:hover {
  color: #000;
}

